import React from 'react';
import { Container, Typography } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { Theme } from '@mui/material/styles';
import { FEDEX_ORANGE, FEDEX_PURPLE } from '../helpers/constants';

const useStyles = makeStyles((theme: Theme) => ({
    container: {
        paddingTop: '4rem',
        paddingBottom: '4rem',
        color: '#fff',
        textAlign: 'justify',
    },
    listItem: {
        marginBottom: theme.spacing(2),
    },
    header:{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
}));

const PrivacyPolicy: React.FC = () => {
    const classes = useStyles();

    return (
        <Container maxWidth="lg" className={classes.container}>
            <div className={classes.header}>
                <Typography variant="h3" gutterBottom style={{ fontFamily: 'Harlow Solid Italics' }}>
                    <span style={{ color: FEDEX_ORANGE }}>S</span>
                    <span style={{ color: FEDEX_PURPLE }}>D</span> Freight Corp.
                </Typography>
                <Typography variant="h3" gutterBottom>
                    PRIVACY POLICY
                </Typography>
            </div>
            <Typography variant="body1" paragraph>
                SD Freight Corp (the “Company”) is committed to maintaining robust privacy protections for its users. Our Privacy Policy (“Privacy Policy”) is designed to help you understand how we collect, use, and safeguard the information you provide to us and to assist you in making informed decisions when using our Service.
            </Typography>
            <Typography variant="body1" paragraph>
                For purposes of this Agreement, “Site” refers to the Company’s website, which can be accessed at https://www.sdfreightcorp.com [or through our mobile application].
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>The Service</strong> refers to the Company’s informational and hiring services, which are accessed exclusively via the Site. Users visiting the website can explore detailed information about SD Freight Corp's values, mission, and the range of services offered. The website serves as a platform to showcase the Company's expertise in logistics and freight management, providing insights into its commitment to quality and customer satisfaction. Additionally, users interested in joining SD Freight Corp's team can access the hiring services section to explore available job positions and submit their applications. While the website primarily offers informational content about the Company's principles and services, users seeking to be a part of the SD Freight Corp workforce can engage with the hiring services provided.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>I. INFORMATION WE COLLECT</strong>
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>We collect “Non-Personal Information” and “Personal Information.”</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                <strong>Non-Personal Information</strong> includes information that cannot be used to personally identify you, such as anonymous usage data, general demographic information we may collect, referring/exit pages and URLs, platform types, preferences you submit, and preferences that are generated based on the data you submit and the number of clicks.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                <strong>Personal Information</strong> includes your email you provide to us through the registration process on our Site, which is required for hiring our services. This may also include other relevant details such as your name, contact information, and any necessary information related to the hiring process.
            </Typography>
            <Typography variant="body1" paragraph>
                <ol>
                    <li className={classes.listItem}>
                        <strong>Information collected via Technology</strong>
                        <p>
                            To access the hiring services on our website, you will need to provide your email address during the registration process. While further personal information is not mandatory for accessing our hiring services, it is required for the hiring process. This additional information includes your name, contact information, employment history, qualifications, and other relevant details necessary for the hiring process.
                        </p>
                        <p>
                            However, as part of our efforts to enhance the quality of our services, we gather certain information through technology. This includes data provided by your browser or our software application when you interact with our website. The collected data encompasses details such as the 'referring URL' (the website that directed you to ours), your browser type, the device you're using, the time and date of your access, and other anonymized information that doesn't personally identify you.
                        </p>
                        <p>
                            We employ cookies, which are small text files containing anonymous unique identifiers, to track this information. Cookies are sent from our servers to your browser and stored on your computer's hard drive. This enables us to gather non-personal information about your interactions with our hiring services and to remember your preferences, both individually and in aggregate. For instance, we might use cookies to track typical things like the number of job listings viewed, application progress, and similar interactions.
                        </p>
                        <p>
                            We utilize both persistent and session cookies. Persistent cookies remain on your computer even after you close your browsing session, until you delete them. Conversely, session cookies expire as soon as you close your browser. As an example, we might store a persistent cookie to track the progression of your job applications and save your preferences for future visits to our hiring portal.
                        </p>
                    </li>
                    <li className={classes.listItem}>
                        <strong>Information you provide us by registering for an account</strong>
                        <p>
                            In addition to the information provided automatically by your browser when you visit the Site, to become a subscriber to the Service you will need to create a personal profile. You can create a profile by registering with the Service and entering your email address, and creating a user name and a password. By registering, you are authorizing us to collect, store, and use your email address in accordance with this Privacy Policy.
                        </p>
                    </li>
                    <li className={classes.listItem}>
                        <strong>Children’s Privacy</strong>
                        <p>
                            The Site and the Service are not directed to anyone under the age of 13. The Site does not knowingly collect or solicit information from anyone under the age of 13, or allow anyone under the age of 13 to sign up for the Service. In the event that we learn that we have gathered personal information from anyone under the age of 13 without the consent of a parent or guardian, we will delete that information as soon as possible. If you believe we have collected such information, please contact us at support@sdfright.com.
                        </p>
                    </li>
                </ol>
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>II. HOW WE USE AND SHARE INFORMATION</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                <strong>Personal Information:</strong>
                Except as otherwise stated in this Privacy Policy, we do not sell, trade, rent, or otherwise share for marketing purposes your Personal Information with third parties without your consent. We do share Personal Information with vendors who are performing services for the Company, such as the servers for our email communications who are provided access to user’s email address for purposes of sending emails from us. Those vendors use your Personal Information only at our direction and in accordance with our Privacy Policy.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                In general, the Personal Information you provide to us is used to help us communicate with you. For example, we use Personal Information to contact users in response to questions, solicit feedback from users, provide technical support, and inform users about promotional offers.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                We may share Personal Information with outside parties if we have a good-faith belief that access, use, preservation, or disclosure of the information is reasonably necessary to meet any applicable legal process or enforceable governmental request; to enforce applicable Terms of Service, including investigation of potential violations; address fraud, security or technical concerns; or to protect against harm to the rights, property, or safety of our users or the public as required or permitted by law.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                <strong>Non-Personal Information</strong>
                In general, we use Non-Personal Information to help us improve the Service and customize the user experience. We also aggregate Non-Personal Information in order to track trends and analyze use patterns on the Site. This Privacy Policy does not limit in any way our use or disclosure of Non-Personal Information, and we reserve the right to use and disclose such Non-Personal Information to our partners, advertisers, and other third parties at our discretion.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                In the event we undergo a business transaction such as a merger, acquisition by another company, or sale of all or a portion of our assets, your Personal Information may be among the assets transferred. You acknowledge and consent that such transfers may occur and are permitted by this Privacy Policy, and that any acquirer of our assets may continue to process your Personal Information as set forth in this Privacy Policy. If our information practices change at any time in the future, we will post the policy changes to the Site so that you may opt out of the new information practices. We suggest that you check the Site periodically if you are concerned about how your information is used.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>III. HOW WE PROTECT INFORMATION</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                We implement security measures designed to protect your information from unauthorized access. Your account is protected by your account password and we urge you to take steps to keep your personal information safe by not disclosing your password and by logging out of your account after each use. We further protect your information from potential security breaches by implementing certain technological security measures including encryption, firewalls, and secure socket layer technology. However, these measures do not guarantee that your information will not be accessed, disclosed, altered or destroyed by breach of such firewalls and secure server software. By using our Service, you acknowledge that you understand and agree to assume these risks.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>IV. YOUR RIGHTS REGARDING THE USE OF YOUR PERSONAL INFORMATION</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                You have the right at any time to prevent us from contacting you for marketing purposes. When we send a promotional communication to a user, the user can opt out of further promotional communications by following the unsubscribe instructions provided in each promotional e-mail. You can also indicate that you do not wish to receive marketing communications from us in the “Settings” section of the Site. Please note that notwithstanding the promotional preferences you indicate by either unsubscribing or opting out in the Settings section of the Site, we may continue to send you administrative emails including, for example, periodic updates to our Privacy Policy.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>V. LINKS TO OTHER WEBSITES</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                As part of the Service, we may provide links to or compatibility with other websites or applications. However, we are not responsible for the privacy practices employed by those websites or the information or content they contain. This Privacy Policy applies solely to information collected by us through the Site and the Service. Therefore, this Privacy Policy does not apply to your use of a third-party website accessed by selecting a link via our Site or via our Service. To the extent that you access or use the Service through or on another website or application, then the privacy policy of that other website or application will apply to your access or use of that site or application. We encourage our users to read the privacy statements of other websites before proceeding to use them.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>VI. CHANGES TO OUR PRIVACY POLICY</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                The Company reserves the right to change this policy and our Terms of Service at any time. We will notify you of significant changes to our Privacy Policy by sending a notice to the primary email address specified in your account or by placing a prominent notice on our site. Significant changes will go into effect 30 days following such notification. Non-material changes or clarifications will take effect immediately. You should periodically check the Site and this privacy page for updates.
            </Typography>
            <Typography variant="body1" paragraph>
                <strong>VII. CONTACT US</strong>
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                If you have any questions regarding this Privacy Policy or the practices of this Site, please contact us by sending an email to support@sdfreightcorp.com.
            </Typography>
            <Typography variant="body1" component="div" paragraph>
                <strong>Last Updated:</strong> This Privacy Policy was last updated in August 2023.
            </Typography>
        </Container>
    );
};

export default PrivacyPolicy;
